@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IM+Fell+English+SC&display=swap);
.about__section{
	min-height: 100vh;
	background-color: #ddd;
}
.about__container{
	width: 80%;
	display: block;
	margin:auto;
	padding-top: 100px;
}
.about__content-section{
	float: left;
	width: 55%;
}
.about__image-section{
	float: right;
	width: 40%;
}
.about__image-section img{
	width: 100%;
	height: auto;
}
.about__content-section .about__title{
	text-transform: uppercase;
	font-size: 28px;
}
.about__content-section .about__content h3{
	margin-top: 20px;
	color:#5d5d5d;
	font-size: 21px;
}
.about__content-section .about__content p{
	margin-top: 10px;
	font-family: sans-serif;
	font-size: 18px;
	line-height: 1.about__5;
}
.about__content-section .about__content .about__button{
	margin-top: 30px;
}
.about__content-section .about__content .about__button a{
	background-color: #3d3d3d;
	padding:12px 40px;
	text-decoration: none;
	color:#fff;
	font-size: 25px;
	letter-spacing: 1.about__5px;
}
.about__content-section .about__social{
	margin: 40px 40px;
}
.about__content-section .about__social i{
	color:#a52a2a;
	font-size: 30px;
	padding:0px 10px;
}
.about__content-section .about__social i:hover{
	color:#3d3d3d;
}
@media screen and (max-width: 768px){
	.about__container{
	width: 80%;
	display: block;
	margin:auto;
	padding-top:50px;
}
.about__content-section{
	float:none;
	width:100%;
	display: block;
	/* margin:auto; */
}
.about__image-section{
	float:none;
	width:100%;
	
}
.about__image-section img{
	width: 100%;
	height: auto;
	display: block;
	margin:auto;
}
.about__content-section .about__title{
	text-align: center;
	font-size: 19px;
}
.about__content-section .about__content .about__button{
	text-align: center;
}
.about__content-section .about__content .about__button a{
	padding:9px 30px;
}
.about__content-section .about__social{
	text-align: center;
}

}
img {
  display: inline-block;
}
.goog-font,
h5 {
  font-family: "IM Fell English SC", serif;
}
h3 {
  margin: auto;
}
.body {
  min-height: 100vh;
}
body {
  overflow-x: hidden;
}
.my-row {
  margin-top: 10px;
  margin-bottom: 10px;
}
.showFullScreenQuery {
  width: 70%;
  height: 40px;
  margin: 10px;
  text-align: left;
  padding: 5px;
}
.showFullScreenQuery:hover {
  border: 3px solid white;
}
.no-border {
  border: 0 solid #ffffff;
}
textarea {
  overflow-y: auto;
}
.d-flex {
  justify-content: center;
}
.blog-item {
  border-radius: 15px;
  height: 12vh;
  overflow: hidden;
}
.spinner {
  height: 100px;
  width: 100px;
}
.cursor:hover {
  cursor: pointer;
}
.grey-bg-hover:hover {
  background-color: hsl(0, 3%, 65%);
  border: 0 transparent white;
}
.grey-bg-hover {
  margin: 20px;
  padding: 10px;
  border: 2px solid hsl(0, 1%, 95%);
}

